import React, { useState, useEffect } from 'react';
import { PageWrapper, InputBlock, YearsBlock, Years, Wrapper, SearchBlock, } from './styles';
import { CardBlock } from '../UI/PhotoBlock/styles';
import Slider from '@material-ui/core/Slider';
import { PhotoBlock } from '../UI/PhotoBlock';
import { TextField, Button } from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';
import Skeleton from '@material-ui/lab/Skeleton';
import { lightTheme, darkTheme } from '../App/styles'
export const Gallery = ({ theme }) => {
    const currentYear = new Date().getFullYear();

    const [query, setQuery] = useState('');
    const [years, setYears] = useState([1920, currentYear]);
    const [mainData, setMainData] = useState([]);
    const [skeleton, setSkeleton] = useState(true);
    const myArray = [1, 2, 3, 4, 5, 6, 7, 8];

    const generateExampleUrl = () => {
        let exampleRequest = `https://images-api.nasa.gov/search?q=${query}&year_start=${years[0]}&year_end=${years[1]}&media_type=image`;

        return exampleRequest;
    }

    const getSearch = async () => {
        try {
            const response = await fetch(generateExampleUrl());
            const data = await response.json();
            setMainData(data.collection.items);
            setSkeleton(true);
            setTimeout(() => setSkeleton(false), 1500);
        } catch (error) {
            throw error;
        }
    }

    const startSearch = (e) => {
        e.preventDefault();
        getSearch();
    }

    useEffect(() => {
        getSearch();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const updateSearch = (e) => {
        setQuery(e.target.value);
    }

    const updateYears = (e, newValue) => {
        setYears(newValue);
    }

    return (
        <PageWrapper>
            <SearchBlock onSubmit={startSearch}>
                <InputBlock>
                    <TextField label='Search here' style={{ background: '#fff', borderRadius: '5px' }} variant="outlined" id="searchInput" type="text" onChange={updateSearch} />
                    <Button
                        style={{ marginLeft: '5px' }}
                        variant="contained"
                        color="primary"
                        startIcon={<SearchIcon />}
                        type="submit"
                    >
                        Search
                    </Button>
                </InputBlock>
                <YearsBlock>
                    <Slider
                        style={{ width: '170px' }}
                        value={years}
                        onChange={updateYears}
                        min={1920}
                        max={currentYear}
                        valueLabelDisplay="auto"
                        aria-labelledby="range-slider"
                    />
                    <Years>{years[0]} - {years[1]}</Years>
                </YearsBlock>
            </SearchBlock>
            <Wrapper>
                {
                    !skeleton ?
                        mainData.map((element, index) => {
                            return (
                                <PhotoBlock
                                    key={element.data[0].nasa_id}
                                    keyName={element.data[0].nasa_id}
                                    src={element.links[0].href}
                                    alt={element.data[0].title}
                                    heading={element.data[0].title}
                                    location={element.data[0].location ? element.data[0].location : 'no location'}
                                    description={element.data[0].description}
                                    date={element.data[0].date_created.slice(0, 10)}
                                    downloadLink={element.links[0].href}
                                    color={theme.thirdBackground}
                                />
                            )
                        }
                        ) :
                        myArray.map((element) => <CardBlock key={element}>
                            <Skeleton height={250} style={{ marginBottom: "5px" }} animation="wave" variant="rect" />
                            <Skeleton animation="wave" />
                            <Skeleton animation="wave" />
                        </CardBlock>)
                }
            </Wrapper>
        </PageWrapper>
    )
}
