import React, { useState } from 'react';
import { CardBlock, CardWrapper, TopBlock, InfoBlock } from './styles';
import { Card, CardActions, IconButton, CardMedia, CardActionArea, Collapse, CardContent, Typography } from '@material-ui/core';
import SaveAltIcon from '@material-ui/icons/SaveAlt';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 345,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
}));

export const PhotoBlock = ({ keyName, color, src, alt, heading, location, description, date, downloadLink }) => {
    const classes = useStyles();
    const [expanded, setExpanded] = useState(false);

    const handleExpandClick = () => {
        setExpanded(!expanded);
    };

    return (
        <CardBlock key={keyName}>
            <CardWrapper style={{ background: color }}>
                <CardActionArea>
                    <CardMedia
                        image={src}
                        alt={alt}
                        title={heading}
                        style={{ height: 0, paddingTop: '70%' }}
                    />
                    <CardActions disableSpacing>
                        <TopBlock>
                            <IconButton
                                aria-label="download"
                                target="_blanc"
                                href={downloadLink}
                            >
                                <SaveAltIcon />
                            </IconButton>
                            <Typography variant="h6" component="h3">{heading}</Typography>
                            <IconButton
                                className={clsx(classes.expand, {
                                    [classes.expandOpen]: expanded,
                                })}
                                onClick={handleExpandClick}
                                aria-expanded={expanded}
                                aria-label="show more"
                            >
                                <ExpandMoreIcon />
                            </IconButton>
                        </TopBlock>
                    </CardActions>
                    <Collapse in={expanded} timeout="auto" unmountOnExit>
                        <CardContent>
                            <InfoBlock>
                                <Typography paragraph>{date}</Typography>
                                <Typography paragraph>{location}</Typography>
                            </InfoBlock>
                            <Typography paragraph>{description}</Typography>
                        </CardContent>
                    </Collapse>
                </CardActionArea>
            </CardWrapper >
        </CardBlock>
    )
}
