import React, { useState } from 'react';
import { APOD } from "../APOD";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import { ThemeProvider } from 'styled-components';
import { GlobalStyle, darkTheme, lightTheme } from "./styles";
import { Navbar1 } from '../Navbar1';
import { Gallery } from "../Gallery";
import { Instructions } from "../Instructions";
import { ThemeTrigger } from "../UI/ThemeTrigger";


const App = () => {
  const [themeState, setThemeState] = useState(darkTheme);

  const trigger = () => {
    if (themeState === darkTheme) {
      setThemeState(lightTheme)
    } else {
      setThemeState(darkTheme)
    }
  }

  return (
    <ThemeProvider theme={themeState}>
      <div className="App">
        <GlobalStyle color={themeState} />
        <Router>
          <Navbar1 element={<ThemeTrigger onChange={trigger} />} />
          <Switch>
            <Route path="/instructions">
              <Instructions />
            </Route>
            <Route path="/gallery">
              <Gallery theme={themeState} />
            </Route>
            <Route path="/">
              <APOD />
            </Route>
          </Switch>
        </Router>
      </div>
    </ThemeProvider>
  );
}

export default App;
