import React from 'react';
import { Wrapper, Heading, MainBlock,Text, MainBlockWrapper, SubHeading } from './styles';

export const Instructions = () => {
    return (
        <Wrapper>
            <Heading>Instructions</Heading>
            <MainBlockWrapper>
                <MainBlock>
                    <SubHeading>Photo of the day</SubHeading>
                    <div>
                        <Text>1. Click on the date picker and enter date. For instance, this could be some memorable date, like someone's birthday etc.</Text>
                        <Text>2. Click on the search button. If you are using PC, you can also hit ENTER key.</Text>
                    </div>
                </MainBlock>
                <MainBlock>
                    <SubHeading>Nasa Gallery</SubHeading>
                    <div>
                        <Text>1. Type your request into "search here" field (if you leave input empty, you'll see some most popular NASA photos on different topics).</Text>
                        <Text>2. See the slider below? Slide it to change years period (personal advice: try not earlier than 1950).</Text>
                        <Text>3. Hit "Search" button or if you are using PC, you can use ENTER key.</Text>
                    </div>
                </MainBlock>
            </MainBlockWrapper>
        </Wrapper>
    )
}
