import styled from 'styled-components';

export const PageWrapper = styled.div`
    background: ${props => props.theme.secondBackground};
    padding-top: 60px;
    min-height: 100vh;

    @media screen and (max-width: 990px){
        padding-top: 80px;
    }
`;

export const Wrapper = styled.div`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 20px 80px;

    @media screen and (max-width: 1440px){
        justify-content: space-around;
    }

    @media screen and (max-width: 965px){
        padding: 0px 30px;
    }

    @media screen and (max-width: 450px){
        padding: 0px 15px; 
    } 
`;

export const Loader = styled.div`
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 150px;
`;

export const LoaderText = styled.p`
    margin-top: 20px;
    font-weight: bold;
    font-size: 20px;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s;

    &.active{
        opacity: 1;
        visibility: visible;
    }
`;

export const SearchBlock = styled.form`
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 20px;
`;

export const InputBlock = styled.div`
    display: flex;
    justify-content: space-between;
`;

export const YearsBlock = styled.div`
    display: flex;
    margin-top: 10px;
    align-items: center;
`;

export const Years = styled.p`
    font-size: 16px;
    margin-left: 20px;
`;