import styled from 'styled-components';

export const Label = styled.label`
   transform: rotate(90deg);
	cursor: pointer;
	text-indent: -9999px;
	width: 40px;
	height: 20px;
	background: grey;
	display: block;
	border-radius: 100px;
	position: relative;
	margin-top: -20px;

    &:after {
	content: '';
	position: absolute;
	top: 5px;
	left: 5px;
	width: 10px;
	height: 10px;
	background: #fff;
	border-radius: 10px;
	transition: 0.3s;
    }

    &:active:after {
	width: 10px;
    }

	@media screen and (max-width:1070px){
		transform: rotate(0deg) scale(1.4);
		margin: -12px 0 0 20px;
	} 
`;

export const Text = styled.p`
    display: none;
	color: ${props => props.theme.mainTextColor};
    margin: 0 20px;
    height: 150%;
    align-items: center;
    transition: 0.15s;
    font-weight: bold;

    @media screen and (max-width: 990px){
        font-weight: 900;
        margin: 20px 0;
        font-size: 27px;
    }    
	
    @media screen and (max-width:1070px){
		display: flex;
	} 
`;