import { createGlobalStyle } from 'styled-components';

export const GlobalStyle = createGlobalStyle`
    *{
        box-sizing: border-box;
        margin: 0;
        padding: 0;
        outline: none;
        font-family: 'Source Sans Pro', sans-serif;
        text-decoration: none;
        scroll-behavior: smooth;
        color: ${props => props.theme.mainTextColor};
        transition: 0.3s;
    }
`;

export const lightTheme = {
    mainTextColor: "#000000",
    oppositeTextColor: "#ffffff",
    skeletonColor: "#868686",
    instructionsBackground: "#f2f2f2",
    instructionsColor: "#000000",
    mainBackground: "#f2f2f2",
    secondBackground: "#f2f2f2",
    thirdBackground: "#e9e9e9",
};

export const darkTheme = {
    mainTextColor: "#ffffff",
    oppositeTextColor: "#000000",
    skeletonColor: "#222222",
    instructionsBackground: "#1f1f1f",
    instructionsColor: "#dc3329",
    mainBackground: "#000000",
    secondBackground: "#222222",
    thirdBackground: "#333333",
};